import { Component, Inject, ViewChild, AfterViewInit } from '@angular/core';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatChipInputEvent } from '@angular/material/chips';
import { FormGroup, FormBuilder, Validators, FormControl } from "@angular/forms";
import { AngularFirestore } from '@angular/fire/firestore';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'add-permission',
  templateUrl: 'addpermission.dialog.html',
})
export class DialogAddPermissionDialog implements AfterViewInit {
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  isDisable = false;
  constructor(public dialogRef: MatDialogRef<DialogAddPermissionDialog>,
    @Inject(MAT_DIALOG_DATA) public data, public fb: FormBuilder, private db: AngularFirestore, private _snackBar: MatSnackBar) {

  }
  myForm: FormGroup;
  ngOnInit(): void {
    this.isDisable = true;
    this.reactiveForm();

  }
  ngAfterViewInit() {
  }
  reactiveForm() {
    this.myForm = this.fb.group({
      name: ['', [Validators.required]],
      mobileNumber: ['', [Validators.required,Validators.minLength(10),
        Validators.maxLength(13)]],
    })
  }


  submitForm() {
    debugger;
    if (localStorage && localStorage.user_data && localStorage.user_data.length && this.myForm.valid) {
      let value = JSON.parse(localStorage.user_data);
      let permissionData: any = this.myForm.value;
      if(permissionData && permissionData.name != '' && permissionData.mobileNumber !=''){
      var timeStamp = new Date().getTime();
      permissionData.id = timeStamp.toString();
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('permission');
      ref.get().subscribe((res) => {
        let allData: any = { 'permission': [] };
        if (res && res.data()) {
          allData = res.data();
        }
        allData.permission.push(permissionData);
        ref.set(allData).then((res: any) => {
          this.openSnackBar("permission Added");
          this.onNoClick();
        });
      })
    }
  }
  }

  deleteUpdateCurrentTeacher(event) {
    event.stopPropagation();
    event.preventDefault();
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      let value = JSON.parse(localStorage.user_data);
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('allteachers');
      if (this.data && this.data.totalData && this.data.ind >= 0 && this.data.status == 'delete') {
        this.data.totalData.splice(this.data.ind, 1);
      }
      if (this.data && this.data.totalData && this.data.ind >= 0 && this.data.status == 'update') {
        let pData = this.data.totalData[this.data.ind];
        let uData = this.myForm.value;
        uData.id = pData.id;
        uData.isPresent = pData.isPresent;
        this.data.totalData[this.data.ind] = uData;
      }
      this.onNoClick();
      ref.update({ 'teachers': this.data.totalData }).then((res: any) => {
        if (this.data.status == 'delete') {
          this.openSnackBar("Teacher Deleted");
        } else if (this.data.status == 'update') {
          this.openSnackBar("Teacher updated");
        }
      });

    }
  }
  openSnackBar(message: string, action?: string) {
    this._snackBar.open(message, action, {
      duration: 1000,
    });
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

}