import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { HttpClient } from "@angular/common/http";
import { LoginService } from "../services/login.service";
import { MatSnackBar } from '@angular/material/snack-bar';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private router: Router, private http: HttpClient, private loginService: LoginService, private _snackBar: MatSnackBar) { }
  totalList: any;
  isDataLoading = false;
  totalCountry = [];
  totalStates: any = [];
  totalDist: any = [];
  allSchool: any = [];
  currentSchool: any = {};
  registerObj: any = {
    country: '',
    state: '',
    district: '',
    schoolName: '',
    address: '',
    username: '',
    password: ''
  };
  loginObj: any = {
    country: '',
    state: '',
    district: '',
    schoolName: '',
    username: '',
    password: ''
  };
  allDistricSchool: any = [];
  ngOnInit() {
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      this.router.navigate(['/main/home']);
    }
    this.http.get("assets/country/countrylist.json").subscribe(data => {
      this.totalList = data;
      this.totalCountry = Object.keys(data)
      console.log(this.totalList);
    })
  }
  onCountrySelection(country) {
    this.totalStates = this.totalList[country];
    this.registerObj.country = country;
    this.totalDist = [];
  }
  onStateSelection(state) {
    var currentState = this.totalStates.filter(function (e) {
      return e.name == state;
    });
    this.registerObj.state = state;
    if (currentState && currentState.length) {
      this.totalDist = currentState[0].districts;
    }
  }
  onDistSelection(district) {
    this.registerObj.district = district;
  }

  onLoginCountrySelection(country) {
    this.totalStates = this.totalList[country];
    this.loginObj.country = country;
    this.totalDist = [];
  }
  onLoginStateSelection(state) {
    var currentState = this.totalStates.filter(function (e) {
      return e.name == state;
    });
    this.loginObj.state = state;
    if (currentState && currentState.length) {
      this.totalDist = currentState[0].districts;
    }
  }
  onLoginDistSelection(district) {
    this.loginObj.district = district;
    this.districtSchool();
  }
  onLoginschoolSelection(school) {
    this.loginObj.schoolName = school;
    this.getSchoolInfo();
  }
  getSchoolInfo() {
    this.loginService.getCurrentSchool(this.loginObj).then((res: any) => {
      if (res) {
        this.currentSchool = res;
      }
    })
  }
  districtSchool() {
    this.isDataLoading = true;
    this.loginService.getAllDistrictSchool(this.loginObj).then((res: any) => {
      this.isDataLoading = false;
      if (res && res.length) {
        this.allDistricSchool = res;
      }
    })
  }


  registerUser(value) {
    this.loginService.registerUser(this.registerObj).then(res => {
      if (res == 'registrated') {
        this.openSnackBar('User Exist go to Login');
      } else {
        this.openSnackBar('Register Sucessfully');
      }
    })
  }

  loginUser() {
    if (this.currentSchool && this.loginObj) {
      if (this.loginObj.username == this.currentSchool.username) {
        if (this.loginObj.password == this.currentSchool.password) {
          delete this.currentSchool.password;
          localStorage['user_data'] = JSON.stringify(this.currentSchool);
          this.router.navigate(['/main']);
        } else {
          this.openSnackBar('password invalid');
        }
      } else {
        this.openSnackBar('username invalid');
      }

    }
  }
  openSnackBar(message: string, action?: string) {
    this._snackBar.open(message, action, {
      duration: 1000,
    });
  }

}
