import { Component, Inject, ViewChild, AfterViewInit } from '@angular/core';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormGroup, FormBuilder, Validators, FormControl } from "@angular/forms";
import { AngularFirestore } from '@angular/fire/firestore';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'add-student',
  templateUrl: 'addstudent.dialog.html',
})
export class DialogAddStudentDialog implements AfterViewInit {
  isDisable = false;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  constructor(public dialogRef: MatDialogRef<DialogAddStudentDialog>,
    @Inject(MAT_DIALOG_DATA) public data, public fb: FormBuilder, private db: AngularFirestore, private _snackBar: MatSnackBar) {

  }
  myClassForm: FormGroup;
  ngOnInit(): void {
    this.isDisable = true;
    this.reactiveForm();
    if (this.data && this.data.totalData && this.data.ind >= 0) {
      let cData = this.data.totalData[this.data.ind];
      if (this.data.status == 'delete') {
        this.isDisable = false;
        this.myClassForm = this.fb.group({
          studentName: new FormControl({ value: cData.className, disabled: true }),
        });
      }
      if (this.data.status == 'update') {
        this.myClassForm = this.fb.group({
          studentName: [cData.className, [Validators.required]],
        })
      }
    }

  }
  ngAfterViewInit() {
  }
  reactiveForm() {
    this.myClassForm = this.fb.group({
      studentName: ['', [Validators.required]],
      rollNumber: ['', [Validators.required]],
      phoneNumber: ['', [Validators.required]],
      parentName: ['', [Validators.required]],
      address: ['', [Validators.required]]
    })
  }


  submitForm() {
    debugger;
    if (localStorage && localStorage.user_data && localStorage.user_data.length && this.myClassForm.valid) {
      let value = JSON.parse(localStorage.user_data);
      let studentData: any = this.myClassForm.value;
      var timeStamp = new Date().getTime();
      studentData.id = timeStamp.toString();

      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).
        doc(value.district).collection(value.schoolName).doc('allClass').collection('Classes').
        doc(this.data.id).collection('students').doc(studentData.rollNumber + '_' + studentData.studentName);
      let studentInfo = { info: studentData }
      ref.set(studentInfo).then((res: any) => {
        this.openSnackBar("Student Added");
        this.onNoClick();
      });
    }
  }
  deleteUpdateCurrentTeacher(event) {
    event.stopPropagation();
    event.preventDefault();
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      let value = JSON.parse(localStorage.user_data);
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('allteachers');
      if (this.data && this.data.totalData && this.data.ind >= 0 && this.data.status == 'delete') {
        this.data.totalData.splice(this.data.ind, 1);
      }
      if (this.data && this.data.totalData && this.data.ind >= 0 && this.data.status == 'update') {
        let pData = this.data.totalData[this.data.ind];
        let uData = this.myClassForm.value;
        uData.id = pData.id;
        uData.isPresent = pData.isPresent;
        this.data.totalData[this.data.ind] = uData;
      }
      this.onNoClick();
      ref.update({ 'teachers': this.data.totalData }).then((res: any) => {
        if (this.data.status == 'delete') {
          this.openSnackBar("Teacher Deleted");
        } else if (this.data.status == 'update') {
          this.openSnackBar("Teacher updated");
        }
      });

    }
  }
  openSnackBar(message: string, action?: string) {
    this._snackBar.open(message, action, {
      duration: 1000,
    });
  }
  onNoClick(): void {
    this.dialogRef.close();
  }

}