import { Component, Inject, ViewChild, AfterViewInit } from '@angular/core';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatChipInputEvent } from '@angular/material/chips';
import { FormGroup, FormBuilder, Validators, FormControl } from "@angular/forms";
import { AngularFirestore } from '@angular/fire/firestore';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'add-teacher',
  templateUrl: 'addteacher.dialog.html',
})
export class DialogAddTeacherDialog implements AfterViewInit {
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  isDisable = false;
  @ViewChild('chipList', { static: true }) chipList;
  GradeArray: any = ['8th Grade', '9th Grade', '10th Grade', '11th Grade', '12th Grade'];
  SubjectsArray: any = [];
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  constructor(public dialogRef: MatDialogRef<DialogAddTeacherDialog>,
    @Inject(MAT_DIALOG_DATA) public data, public fb: FormBuilder, private db: AngularFirestore, private _snackBar: MatSnackBar) {

  }
  myForm: FormGroup;
  ngOnInit(): void {
    this.isDisable = true;
    this.reactiveForm();
    if (this.data && this.data.totalData && this.data.ind >= 0) {
      let cData = this.data.totalData[this.data.ind];
      this.SubjectsArray = cData.subjects;
      if (this.data.status == 'delete') {
        this.removable = false;
        this.isDisable = false;
        this.myForm = this.fb.group({
          name: new FormControl({ value: cData.name, disabled: true }),
          email: new FormControl({ value: cData.email, disabled: true }),
          mobileNumber: new FormControl({ value: cData.mobileNumber, disabled: true }),
          gender: new FormControl({ value: cData.gender, disabled: true }),
          dob: new FormControl({ value: cData.dob, disabled: true }),
          //   grade: [''],
          subjects: new FormControl({ value: this.SubjectsArray, disabled: true }),
        });
      }
      if (this.data.status == 'update') {
        this.myForm = this.fb.group({
          name: [cData.name, [Validators.required]],
          email: [cData.email, [Validators.required]],
          mobileNumber: [cData.mobileNumber, [Validators.required]],
          gender: [cData.gender],
          dob: [cData.dob, [Validators.required]],
          //   grade: [''],
          subjects: [this.SubjectsArray]
        })
      }
    }

  }
  ngAfterViewInit() {
  }
  /* Reactive form */
  reactiveForm() {
    this.myForm = this.fb.group({
      name: ['', [Validators.required]],
      email: ['', [Validators.required]],
      mobileNumber: ['', [Validators.required]],
      gender: ['Male'],
      dob: ['', [Validators.required]],
      //   grade: [''],
      subjects: [this.SubjectsArray]
    })
  }
  /* Date */
  date(e) {
    var convertDate = new Date(e.target.value).toISOString().substring(0, 10);
    this.myForm.get('dob').setValue(convertDate, {
      onlyself: true
    })
  }

  /* Add dynamic languages */
  add(event: MatChipInputEvent): void {
    if (this.data.status == 'add') {
      const input = event.input;
      const value = event.value;
      if ((value || '').trim() && this.SubjectsArray.length < 5) {
        this.SubjectsArray.push({ name: value.trim() })
      }
      // Reset the input value
      if (input) {
        input.value = '';
      }
    }
  }

  /* Remove dynamic languages */
  remove(subject): void {
    const index = this.SubjectsArray.indexOf(subject);
    if (index >= 0) {
      this.SubjectsArray.splice(index, 1);
    }
  }
  submitForm() {
    debugger;
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      let value = JSON.parse(localStorage.user_data);
      let teacherData: any = this.myForm.value;
      var timeStamp = new Date().getTime();
      teacherData.id = timeStamp.toString();
      teacherData.isPresent = false;
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('allteachers');
      ref.get().subscribe((res) => {
        let allData: any = { 'teachers': [] };
        if (res && res.data()) {
          allData = res.data();
        }
        allData.teachers.push(teacherData);
        ref.set(allData).then((res: any) => {
          this.openSnackBar("Teacher Added");
          this.onNoClick();
        });
      })
    }
  }
  deleteUpdateCurrentTeacher(event) {
    event.stopPropagation();
    event.preventDefault();
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      let value = JSON.parse(localStorage.user_data);
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('allteachers');
      if (this.data && this.data.totalData && this.data.ind >= 0 && this.data.status == 'delete') {
        this.data.totalData.splice(this.data.ind, 1);
      }
      if (this.data && this.data.totalData && this.data.ind >= 0 && this.data.status == 'update') {
        let pData = this.data.totalData[this.data.ind];
        let uData = this.myForm.value;
        uData.id = pData.id;
        uData.isPresent = pData.isPresent;
        this.data.totalData[this.data.ind] = uData;
      }
      this.onNoClick();
      ref.update({ 'teachers': this.data.totalData }).then((res: any) => {
        if (this.data.status == 'delete') {
          this.openSnackBar("Teacher Deleted");
        } else if (this.data.status == 'update') {
          this.openSnackBar("Teacher updated");
        }
      });

    }
  }
  openSnackBar(message: string, action?: string) {
    this._snackBar.open(message, action, {
      duration: 1000,
    });
  }
  onNoClick(): void {
    this.dialogRef.close();
  }

}