import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DialogAddPermissionDialog } from './addpremission.dialog';
import { AngularFirestore } from '@angular/fire/firestore';

let allPermissionData: any = [];
@Component({
  selector: 'app-permission',
  templateUrl: './permission.component.html',
  styleUrls: ['./permission.component.css']
})
export class PermissionComponent implements OnInit {
  displayedColumns: string[] = ['id', 'name', 'mobileNumber', 'edit', 'delete'];
  dataSource = new MatTableDataSource<any>(allPermissionData);
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  isDataLoaded = false;
  constructor(public dialog: MatDialog, private db: AngularFirestore) { }

  ngOnInit() {
    this.dataSource.paginator = this.paginator;
    this.getpermissionUserData();
  }
  getpermissionUserData() {
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      this.isDataLoaded = true;
      let value = JSON.parse(localStorage.user_data);
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('permission');
      ref.snapshotChanges().subscribe((res: any) => {
        if (res && res.payload && res.payload.data()) {
          allPermissionData = res.payload.data().permission;
          this.dataSource = new MatTableDataSource<any>(allPermissionData);
          this.isDataLoaded = false;
          console.log(allPermissionData);
        }
      })

    }
  }

  openPermissionDialog(status?, index?) {
    const dialogRef = this.dialog.open(DialogAddPermissionDialog, {
      width: '300px',
      minWidth: '250px',
      data: { totalData: allPermissionData, ind: index, status: status }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

    });
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

}
