import { Component, OnInit } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { MatDialog } from '@angular/material/dialog';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { DialogAddClassDialog } from 'src/app/dialog/addclass/addclass.dialog';
import { AngularFirestore } from '@angular/fire/firestore';
@Component({
  selector: 'app-mainpage',
  templateUrl: './mainpage.component.html',
  styleUrls: ['./mainpage.component.css']
})
export class MainpageComponent implements OnInit {
 userData:any={};
 allClassList:any=[];
 panelOpenState = false;
  constructor(private http:HttpClient,private router:Router,public dialog: MatDialog,private db: AngularFirestore) { }
  ngOnInit() {
    if(localStorage && localStorage.user_data && localStorage.user_data.length){
      this.router.navigate(['/main/home']);
      this.userData=JSON.parse(localStorage.user_data);
    }else{
      this.router.navigate(['/login']);
    }
  this.getAllClassList(this.userData);
  }
  getAllClassList(value){
    let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('allClass').collection('Classes');
    ref.snapshotChanges().subscribe((res: any) => {
      if (res && res.length) {
        // res[0].payload.doc.data()
        this.allClassList = res;
        console.log(this.allClassList);
      }
    })
  }
  logoutUser(){
    localStorage.removeItem("user_data");
    this.router.navigate(['/login']);
  }
  navigate(ind) {
    this.router.navigate( ['grade'], { queryParams: { jwt: JSON.stringify(ind)}});
  }
  addSchoolClass(evt){
   evt.stopPropagation();
   evt.preventDefault();
    const dialogRef = this.dialog.open(DialogAddClassDialog, {
      width: '300px',
      minWidth: '250px',
      data: { totalData: '',  status: 'add' }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

    });
  }
 
}
