import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DialogAddTeacherDialog } from '../../dialog/addteacher/addteacher.dialog';
import { AngularFirestore } from '@angular/fire/firestore';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { ThemePalette } from '@angular/material/core';
@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.css']
})
export class TeacherComponent implements OnInit {
  allTeacherData: any = [];
  isDataLoaded: boolean = false;
  slideColor = 'primary';
  constructor(public dialog: MatDialog, private db: AngularFirestore) { }
  ngOnInit() {
    this.getTeacherData();
  }
  getTeacherData() {
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      this.isDataLoaded = true;
      let value = JSON.parse(localStorage.user_data);
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('allteachers');
      ref.snapshotChanges().subscribe((res: any) => {
        if (res && res.payload && res.payload.data()) {
          this.allTeacherData = res.payload.data().teachers;
          this.isDataLoaded = false;
          console.log(this.allTeacherData);
        }
      })

    }
  }
  teacherAttendence(event, ind) {
    console.log('toggle', event.checked);
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      let value = JSON.parse(localStorage.user_data);
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('allteachers');
      this.allTeacherData[ind].isPresent = event.checked;
      ref.update({ 'teachers': this.allTeacherData }).then((res: any) => {
        //  this.openSnackBar("status updated");
        console.log("Updated");
      });

    }
  }
  dropTeacher(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.allTeacherData, event.previousIndex, event.currentIndex);
  }
  deleteCurrentTeacher(ind) {
    this.openTeacherDialog('delete', ind);
  }
  updateCurrentTeacher(ind) {
    this.openTeacherDialog('update', ind);
  }
  openTeacherDialog(status?, index?) {
    const dialogRef = this.dialog.open(DialogAddTeacherDialog, {
      width: '300px',
      minWidth: '250px',
      data: { totalData: this.allTeacherData, ind: index, status: status }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

    });
  }

}
